'use client'

import { useState, useEffect } from 'react'
import { Card } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { ChallengeWithCompletion } from '@/types'

export default function ChallengesPage() {
  const [challenges, setChallenges] = useState<ChallengeWithCompletion[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('ALL')

  useEffect(() => {
    fetchChallenges()
  }, [])

  const fetchChallenges = async () => {
    try {
      const res = await fetch('/api/challenges')
      if (!res.ok) {
        throw new Error('Failed to fetch challenges')
      }
      const data = await res.json()
      setChallenges(data)
    } catch (err: any) {
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }

  const getDifficultyColor = (difficulty: string) => {
    const colors = {
      EASY: 'bg-green-500',
      MEDIUM: 'bg-yellow-500',
      HARD: 'bg-red-500',
      INSANE: 'bg-purple-500',
    }
    return colors[difficulty as keyof typeof colors] || 'bg-gray-500'
  }

  const categories = ['ALL', 'WEB', 'CRYPTO', 'FORENSICS', 'REVERSE', 'PWN', 'MISC']

  const filteredChallenges = selectedCategory === 'ALL'
    ? challenges
    : challenges.filter(challenge => challenge.category === selectedCategory)

  if (loading) {
    return (
      <div className="min-h-screen bg-black p-8">
        <div className="container mx-auto">
          <div className="animate-pulse space-y-4">
            {[1, 2, 3].map(i => (
              <div key={i} className="h-48 bg-white/5 rounded-lg" />
            ))}
          </div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen bg-black p-8">
        <div className="container mx-auto">
          <div className="bg-red-500/10 border border-red-500/20 rounded-lg p-4 text-red-400">
            {error}
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black p-8">
      <div className="container mx-auto">
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-4">Challenges</h1>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
            <Card className="bg-white/5 p-4">
              <h3 className="font-semibold mb-2">Total Challenges</h3>
              <p className="text-2xl">{challenges.length}</p>
            </Card>
            <Card className="bg-white/5 p-4">
              <h3 className="font-semibold mb-2">Completed</h3>
              <p className="text-2xl">
                {challenges.filter(c => c.completed).length}
              </p>
            </Card>
            <Card className="bg-white/5 p-4">
              <h3 className="font-semibold mb-2">Progress</h3>
              <Progress 
                value={(challenges.filter(c => c.completed).length / challenges.length) * 100} 
                className="mt-2"
              />
            </Card>
            <Card className="bg-white/5 p-4">
              <h3 className="font-semibold mb-2">Points Earned</h3>
              <p className="text-2xl">
                {challenges
                  .filter(c => c.completed)
                  .reduce((acc, curr) => acc + curr.points, 0)}
              </p>
            </Card>
          </div>
        </div>

        <Tabs defaultValue={selectedCategory} onValueChange={setSelectedCategory}>
          <TabsList className="bg-white/5 p-1">
            {categories.map(category => (
              <TabsTrigger
                key={category}
                value={category}
                className="data-[state=active]:bg-white data-[state=active]:text-black"
              >
                {category}
              </TabsTrigger>
            ))}
          </TabsList>

          <TabsContent value={selectedCategory} className="mt-6">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredChallenges.map(challenge => (
                <Card
                  key={challenge.id}
                  className={`bg-white/5 border border-white/10 hover:border-white/20 transition-colors ${
                    challenge.completed ? 'opacity-50' : ''
                  }`}
                >
                  <div className="p-6">
                    <div className="flex items-center justify-between mb-4">
                      <Badge className={getDifficultyColor(challenge.difficulty)}>
                        {challenge.difficulty}
                      </Badge>
                      <span className="text-sm text-gray-400">{challenge.points} pts</span>
                    </div>
                    <h3 className="text-xl font-semibold mb-2">{challenge.title}</h3>
                    <p className="text-gray-400 mb-4 line-clamp-2">
                      {challenge.description}
                    </p>
                    <div className="flex items-center justify-between">
                      <Badge variant="outline">
                        {challenge.category}
                      </Badge>
                      <span className="text-sm text-gray-400">
                        {challenge._count.completedBy} solves
                      </span>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
